.List {
  right: 0;
  background-color: rgba(255, 255, 255, 0.9);
}

.list-header {
    height: 130px;
  border-bottom: 0.5px solid rgba(151, 151, 151, 0.9);
}

.logo-image {
  margin-left: 2px;
}

.filter-title {
  white-space: nowrap;
  padding: 0 5px 0 5px;
  cursor: pointer;
  font-weight: 500;
  margin: 0.2em;
  border-radius: 0.3em;
  line-height: 27px;
  height: 27px;
}

.list-container {
  overflow: scroll;
  height: 100%;
}

.ReactVirtualized__List {
    outline: none;
}

.blur-bottom-corner {
    position: absolute;
    bottom: 29px;
}


.ReactVirtualized__List::-webkit-scrollbar {
  background-color: Transparent;
  width: 5px;
}
.ReactVirtualized__List::-webkit-scrollbar-thumb {
  background-color: rgb(173, 174, 177);
  border-radius: 1rem;
}

li {
  list-style-type: none; /* Убираем маркеры */
}

ul {
  margin-left: 0; /* Отступ слева в браузере IE и Opera */
  padding-left: 0; /* Отступ слева в браузере Firefox, Safari, Chrome */
}

.filter-item {
  width: 25px;
  margin: 0px 2px;
}

.list-item {
  font-weight: 400;
  height: 40px;
  border-bottom: 0.5px solid rgba(151, 151, 151, 0.9);
  cursor: pointer;
  white-space: nowrap;
}

.list-item>div {
  display: inline-block;
}

.list-item .name {
  padding: 0;
  margin: 0;
  white-space: nowrap;
  overflow: hidden;
  text-overflow: ellipsis;
}

.list-item:hover {
  background-color: #EBEDF0;
}

.list-item .filter-item {
  font-weight: 600;
  border-radius: 0.25em;
  min-width: 25px;
}

.remain-info {
    color: #929394;
    position: absolute;
    bottom: 0;
    background-color: rgba(255,255,255,0.8);
}

.exit {
  cursor: pointer;
}

.exit img {
  transition: 0.2s;
}

.exit img:hover {
  transform: scale(1.2);
}
