.waiter {
  position: absolute;
  z-index: 3;
  background-color: rgba(255, 255, 255, 0.8);
}

.logo {
    position: absolute;
  opacity: .8;
}

.auth {
  position: absolute;
  z-index: 2;
  background-color: rgba(255, 255, 255, 0.8);
  font-size: 1.1em;
}

input {
  width: 300px;
  outline: none;
  padding-left: 5px;
  border: 1px solid #D5D6D6;
  border-radius: 0.3em;
  box-shadow: inset 1px 1px 3px 0px #D5D6D6;
}

.waiting {
  cursor: wait;
}

.btn {
  background-color: #6DAD9D !important;
  border-color: #6DAD9D !important;
  line-height: 100%;
  height: 30px;
  width: 80px;
}

.btn:hover {
  cursor: pointer !important;
}

.btn:focus, .btn:active {
  outline: none !important;
  box-shadow: none !important;
}

.warning {
  height: 50px;
  color: #C93638;
}
.cluster {
  width: 30px;
  height: 30px;
  color: #fff;
  margin-left: -50%;
  border-radius: 100%;
  display: -webkit-flex;
  display: flex;
  cursor: pointer;
}

.cluster .title {
  font-size: 16px;
}

.map {
  position: absolute;
  z-index: 0;
}

.Interface {
    position: fixed;
    z-index: 10;
    min-width: 800px;
}

.Interface.frame-closed {
    min-width: unset;
    right: 0;
}
.Frame iframe {
    background-color: white;
    border: none;
}

.iframe-container {
    height: 100%;
    width: 100%;
    position: relative;
}

.close-button {
    cursor: pointer;
    position: absolute;
    right: -1.7rem;
    font-size: 1.5rem;
    font-weight: 700;
    line-height: 1;
    color: #000;
    text-shadow: 0 1px 0 #fff;
    opacity: .5;
    transition: 0.2s;
}

.close-button:hover {
    opacity: .9;
}
.List {
  right: 0;
  background-color: rgba(255, 255, 255, 0.9);
}

.list-header {
    height: 130px;
  border-bottom: 0.5px solid rgba(151, 151, 151, 0.9);
}

.logo-image {
  margin-left: 2px;
}

.filter-title {
  white-space: nowrap;
  padding: 0 5px 0 5px;
  cursor: pointer;
  font-weight: 500;
  margin: 0.2em;
  border-radius: 0.3em;
  line-height: 27px;
  height: 27px;
}

.list-container {
  overflow: scroll;
  height: 100%;
}

.ReactVirtualized__List {
    outline: none;
}

.blur-bottom-corner {
    position: absolute;
    bottom: 29px;
}


.ReactVirtualized__List::-webkit-scrollbar {
  background-color: Transparent;
  width: 5px;
}
.ReactVirtualized__List::-webkit-scrollbar-thumb {
  background-color: rgb(173, 174, 177);
  border-radius: 1rem;
}

li {
  list-style-type: none; /* Убираем маркеры */
}

ul {
  margin-left: 0; /* Отступ слева в браузере IE и Opera */
  padding-left: 0; /* Отступ слева в браузере Firefox, Safari, Chrome */
}

.filter-item {
  width: 25px;
  margin: 0px 2px;
}

.list-item {
  font-weight: 400;
  height: 40px;
  border-bottom: 0.5px solid rgba(151, 151, 151, 0.9);
  cursor: pointer;
  white-space: nowrap;
}

.list-item>div {
  display: inline-block;
}

.list-item .name {
  padding: 0;
  margin: 0;
  white-space: nowrap;
  overflow: hidden;
  text-overflow: ellipsis;
}

.list-item:hover {
  background-color: #EBEDF0;
}

.list-item .filter-item {
  font-weight: 600;
  border-radius: 0.25em;
  min-width: 25px;
}

.remain-info {
    color: #929394;
    position: absolute;
    bottom: 0;
    background-color: rgba(255,255,255,0.8);
}

.exit {
  cursor: pointer;
}

.exit img {
  transition: 0.2s;
}

.exit img:hover {
  -webkit-transform: scale(1.2);
          transform: scale(1.2);
}

