.logo {
    position: absolute;
  opacity: .8;
}

.auth {
  position: absolute;
  z-index: 2;
  background-color: rgba(255, 255, 255, 0.8);
  font-size: 1.1em;
}

input {
  width: 300px;
  outline: none;
  padding-left: 5px;
  border: 1px solid #D5D6D6;
  border-radius: 0.3em;
  box-shadow: inset 1px 1px 3px 0px #D5D6D6;
}

.waiting {
  cursor: wait;
}

.btn {
  background-color: #6DAD9D !important;
  border-color: #6DAD9D !important;
  line-height: 100%;
  height: 30px;
  width: 80px;
}

.btn:hover {
  cursor: pointer !important;
}

.btn:focus, .btn:active {
  outline: none !important;
  box-shadow: none !important;
}

.warning {
  height: 50px;
  color: #C93638;
}