.Frame iframe {
    background-color: white;
    border: none;
}

.iframe-container {
    height: 100%;
    width: 100%;
    position: relative;
}

.close-button {
    cursor: pointer;
    position: absolute;
    right: -1.7rem;
    font-size: 1.5rem;
    font-weight: 700;
    line-height: 1;
    color: #000;
    text-shadow: 0 1px 0 #fff;
    opacity: .5;
    transition: 0.2s;
}

.close-button:hover {
    opacity: .9;
}